.wp-block-cr-btn-cr-btn {
	text-decoration: none !important;
	color:#00b4ff !important;
}

.let-us-talk-btn{
	text-decoration: none !important;
	color:#00b4ff !important;
}
.cr-btn-default {
    display: inline-block;
    height: 60px;
    line-height: 62px;
    padding: 0px 50px;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    border-radius: 50px;
    font-weight: 700;
    position: relative;
    z-index: 1;
    cursor: pointer;
}

.svg-btn-effect {
    position: relative;
    display: inline-block; 
}
.svg-btn-effect svg {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%; 
}
.svg-btn-effect svg rect {
    fill: none;
    stroke: #00b4ff;
    stroke-dasharray: 353, 0;
    stroke-width: 2px;
    stroke-dashoffset: 0;
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -ms-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease; 
}
.svg-btn-effect:hover svg rect {
    stroke-dasharray: 154, 537;
    stroke-dashoffset: 420; 
}

.svg-btn-effect.lets-talk-btn:hover svg rect {
    stroke-dasharray: 94,544;
    stroke-dashoffset: 410;
}
.svg-btn-effect.talk-us-btn:hover svg rect {
    stroke-dasharray: 114, 497;
    stroke-dashoffset: 358;
}
.svg-btn-effect.let-us-talk-btn:hover svg rect {
    stroke-dasharray: 106,552;
}
